import { Genero } from './../models/genero.model';

export class AllGenero {
    public generos: Genero[];

    masculino: Genero = new Genero(1, 'Masculino');
    feminino: Genero = new Genero(2, 'Feminino');

    constructor(     
    ){       
        this.generos = [this.masculino, this.feminino];
    }
}


