import { AllGenero } from './../../../constants/generos.constant';
import { AllPorte } from './../../../constants/portes.constant';
import { UserService } from './../../../providers/user.service';
import { PetService } from './../../../providers/pet.service';
import { UtilsService } from './../../../providers/utils.service';
import { ImageService } from './../../../providers/image.service';
import { PageTitle } from './../../../pipes/page-title';
import { FormGroup, FormBuilder, Validators } from '@angular/forms';
import { Component } from '@angular/core';
import { IonicPage, NavController, NavParams, Loading } from 'ionic-angular';
import { Genero } from './../../../models/genero.model';
import { Pet } from './../../../models/pet.model';
import { Porte } from '../../../models/porte.model';

@IonicPage()
@Component({
  selector: 'page-edit-pet',
  templateUrl: 'edit-pet.html',
})
export class EditPetPage {

  public title: string;
  public pet: any;
  public portes: Porte[];
  public generos: Genero[];
  private image: string;
  petForm: FormGroup;

  constructor(public navCtrl: NavController,
    public navParams: NavParams,
    public pageTitlePipe: PageTitle,
    public formBuilder: FormBuilder,
    public utilsService: UtilsService,
    public petService: PetService,
    public userService: UserService,
    public imageService: ImageService) {

    //Recebe Pet para Edição
    this.pet = navParams.get('pet') || undefined;

    this.petForm = this.formBuilder.group({
      nome: ['', [Validators.required, Validators.minLength(2)]],
      dataNascimento: [''],
      raca: ['', [Validators.required, Validators.minLength(5)]],
      peso: ['', [Validators.required]],
      porte: [''],
      genero: [''],
    });

    this.portes = new AllPorte().portes;
    this.generos = new AllGenero().generos;
  }

  ionViewDidLoad(): void {
    if (!this.pet) { this.title = this.pageTitlePipe.transform('Novo Pet'); }
    else { this.title = this.pageTitlePipe.transform('Editar Pet'); }

    //Preenche formulário com objeto Pet para Edição
    if (this.pet) {
      this.petForm.patchValue({
        nome: this.pet.nome,
        dataNascimento: this.pet.dataNascimento,
        raca: this.pet.raca,
        peso: this.pet.peso,
        porte: this.pet.porte,
        genero: this.pet.genero,
      });
    }
  }

  onSubmit(): void {
    let formPet = this.petForm.value;
    let uuid = this.userService.currentUserUid;

    if (!this.pet) { this.newPet(formPet, uuid) }
    else { this.updatePet(formPet, uuid, this.pet.$key) }
  }

  newPet(formPet: any, uuid: string) {
    let loading: Loading = this.utilsService.showLoading();

    this.petService.create(formPet, uuid)
      .then(() => {
        loading.dismiss();
        this.utilsService.showAlert("Pet cadastrado com sucesso!");
        this.navCtrl.pop();
      })
      .catch((error: any) => {
        loading.dismiss();
        this.utilsService.showAlert(error);
        this.navCtrl.pop();
      });
  }

  updatePet(formPet: any, uuid: string, petUuid: string) {
    let loading: Loading = this.utilsService.showLoading();

    this.petService.update(formPet, uuid, petUuid)
      .then(() => {
        loading.dismiss();
        this.utilsService.showAlert("Pet alterado com sucesso!");
        this.navCtrl.pop();
      })
      .catch((error: any) => {
        loading.dismiss();
        this.utilsService.showAlert(error);
        this.navCtrl.pop();
      });
  }

  takePicture() {
    this.imageService.takePicture(this.pet.$key);
    this.image = localStorage.getItem('image');
  }

}



