import { Agendamento } from './../models/agendamento.model';
import { AngularFire } from 'angularfire2';
import { Pet } from './../models/pet.model';
import { Injectable } from '@angular/core';
import { BaseService } from './base.service';

@Injectable()
export class SchedulingService extends BaseService {

    constructor(
        public af: AngularFire
    ){
        super();
    }

    create(agendamento: Agendamento, userUuid: string, petUuid?: string): firebase.Promise<void> {
        return this.af.database.object(`/schedules/${userUuid}/${'-Kz0QD2v-srBBD1NwWi0'}/${this.generateKey()}`)
          .set(agendamento)
          .catch(this.handlePromiseError);
    }
}


