import { Porte } from "../models/porte.model";

export class AllPorte {
    public portes: Porte[];

    pequeno: Porte = new Porte(1, 'Pequeno');
    medio: Porte = new Porte(2, 'Médio');
    grande: Porte = new Porte(3, 'Grande');

    constructor(     
    ){       
        this.portes = [this.pequeno, this.medio, this.grande];
    }
}


