import { UserService } from './../../../providers/user.service';
import { UtilsService } from './../../../providers/utils.service';
import { SchedulingService } from './../../../providers/scheduling.service';
import { FormGroup, FormBuilder, Validators } from '@angular/forms';
import { Component } from '@angular/core';
import { IonicPage, NavController, NavParams, Loading } from 'ionic-angular';

@IonicPage()
@Component({
  selector: 'page-edit-scheduling',
  templateUrl: 'edit-scheduling.html',
})
export class EditSchedulingPage {
  public title: string;
  public agendamento: any;
  schedulingForm: FormGroup;

  constructor(
    public navCtrl: NavController,
    public navParams: NavParams,
    public formBuilder: FormBuilder,
    public schedulingService: SchedulingService,
    public utilsService: UtilsService,
    public userService: UserService
  ) {

    this.agendamento = navParams.get('agendamento') || undefined;

    this.schedulingForm = this.formBuilder.group({
      titulo: ['', [Validators.required, Validators.minLength(2)]],
      descricao: ['', [Validators.required, Validators.minLength(2)]],
    });
  }

  ionViewDidLoad() {
    if (!this.agendamento) { this.title = 'Novo Agendamento'; }
    else { this.title = 'Editar Agendamento'; }
  }

  onSubmit() {
    let formScheduling = this.schedulingForm.value;
    let uuid = this.userService.currentUserUid;

    if (!this.agendamento) { this.newScheduling(formScheduling, uuid) }
    // else { this.updateScheduling(formScheduling, uuid, this.agendamento.$key) }
  }

  newScheduling(formScheduling: any, uuid: string) {
    let loading: Loading = this.utilsService.showLoading();

    this.schedulingService.create(formScheduling, uuid)
      .then(() => {
        loading.dismiss();
        this.utilsService.showAlert("Agendamento com sucesso!");
        this.navCtrl.pop();
      })
      .catch((error: any) => {
        loading.dismiss();
        this.utilsService.showAlert(error);
        this.navCtrl.pop();
      });
  }

  //updateScheduling(formScheduling: any, uuid: string, schedulingUuid: string) {
  //   let loading: Loading = this.utilsService.showLoading();

  //  this.petService.update(formPet, uuid, petUuid)
  //    .then(() => {
  //      loading.dismiss();
  //this.utilsService.showAlert("Pet alterado com sucesso!");
  //      this.navCtrl.pop();
  //    })
  //    .catch((error: any) => {
  //     loading.dismiss();
  //      this.utilsService.showAlert(error);
  //      this.navCtrl.pop();
  //    });
  // }

}



